/* Hide the "Docs" title in the sidebar */
.nextra-sidebar-container > nav > a:first-child {
  display: none !important;
}

/* Hide the breadcrumb */
.nextra-breadcrumb {
  display: none !important;
}

.nextra-navbar {
  background-color: hsl(
    0,
    0%,
    100%
  ) !important; /* Light yellow for light mode */
}

.nextra-navbar-blur {
  background-color: hsl(
    0,
    0%,
    100%
  ) !important; /* Light yellow for light mode */
}

/* Override Nextra background color and other root variables */
:root {
  --nextra-primary-hue: 45deg; /* Changed to yellow (45 degrees) */
  --nextra-primary-saturation: 100%;
  --nextra-primary-lightness: 50%; /* Adjusted for better visibility */
  --nextra-bg: 250, 250, 250;
  --nextra-content-width: 90rem;
  --nextra-primary-bg: #ffffff !important;
}

.dark {
  --nextra-primary-hue: 45deg; /* Changed to yellow (45 degrees) */
  --nextra-primary-saturation: 100%;
  --nextra-primary-lightness: 60%; /* Slightly lighter for dark mode */
  --nextra-bg: 17, 17, 17;
  --nextra-primary-bg: #000000 !important;
}

::selection {
  background: hsla(
    var(--nextra-primary-hue),
    var(--nextra-primary-saturation),
    var(--nextra-primary-lightness),
    0.3
  );
}

html {
  background: rgb(var(--nextra-bg));
}
